<template>
    <div class="Register">
        <v-app>
            <v-app-bar app color="primary" dark>
            <v-toolbar-title>Vuetify</v-toolbar-title>
            <v-spacer />
            <v-btn to="/" text rounded>Login</v-btn>
            </v-app-bar>
            <v-main>
            
            <!-- Login Module -->
            <v-card width="400" class="mx-auto mt-5" :loading="dialog">
                <v-card-title class="headline mb-1">
                    Register
                </v-card-title>
                <v-card-text>
                <v-form>
                    <v-text-field
                    label="Name"
                    prepend-inner-icon="mdi-account-circle"
                    outlined
                    />
                    <v-text-field
                    label="Email"
                    prepend-inner-icon="mdi-email"
                    outlined
                    />
                    <v-text-field 
                    :type="showPassword ? 'text' : 'password'"
                    label="Password"
                    prepend-inner-icon="mdi-lock"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    outlined
                    @click:append="showPassword = !showPassword"
                    />
                    <v-btn
                        :disabled="dialog"
                        :loading="dialog"
                        tile
                        color="info"
                        @click="dialog = true"
                        >
                        Register
                        </v-btn>
                </v-form>
                </v-card-text>
            </v-card>

            <v-snackbar
                v-model="snackbar"
                >
                {{ text }}

                <template v-slot:action="{ attrs }">
                    <v-btn
                    color="red"
                    text
                    v-bind="attrs"
                    @click="snackbar = false"
                    >
                    Close
                    </v-btn>
                </template>
            </v-snackbar>

            </v-main>

        </v-app>
    </div>
</template>

<script>

export default {
    name: 'Register',
    components: {
    },

    data: () => ({
        showPassword: false,
        active: false,
        dialog: false,
        snackbar: false,
        text: 'Not Updated Yet !!'
    }),

    watch: {
        dialog (val) {
            if (!val) return

            setTimeout(() => (this.dialog = false, this.snackbar = true), 4000)
        },
    },
};
</script>
