<template>
    <div class="Login">
        <v-app>
            <v-app-bar app color="primary" dark>
            <v-toolbar-title>Vuetify</v-toolbar-title>
            <v-spacer />
            <v-btn to="/register" text rounded>Register</v-btn>
            </v-app-bar>
            <v-main>

            <v-progress-linear
                indeterminate
                color="indigo"
                :active="google"
            ></v-progress-linear>
            
            <!-- Login Module -->
            <v-card width="400" class="mx-auto mt-5" :loading="dialog">
                <v-card-title class="headline mb-1">
                    Login
                </v-card-title>
                <v-card-text>
                <v-form>
                    <v-text-field
                    label="Email" 
                    prepend-inner-icon="mdi-email"
                    outlined
                    />
                    <v-text-field 
                    :type="showPassword ? 'text' : 'password'"
                    label="Password"
                    prepend-inner-icon="mdi-lock"
                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                    outlined
                    @click:append="showPassword = !showPassword"
                    />
                    <v-btn
                        :disabled="dialog"
                        :loading="dialog"
                        tile
                        color="info"
                        @click="dialog = true"
                        >
                        Login
                    </v-btn>
                    
                    <v-btn class="ml-5" outlined tile color="info">
                        <v-img
                            :src="require('../assets/google.svg')"
                            contain
                            width="28"
                            class="mr-5"
                        />
                        Google Login
                    </v-btn>
                </v-form>
                </v-card-text>
            </v-card>


            <!-- <v-dialog
                v-model="please"
                hide-overlay
                persistent
                width="300"
                >
                <v-card
                    color="primary"
                    dark
                >
                    <v-card-text>
                    Please stand by
                    <v-progress-linear
                        indeterminate
                        color="white"
                        class="mb-0"
                    ></v-progress-linear>
                    </v-card-text>
                </v-card>
            </v-dialog> -->

            <v-snackbar
                v-model="snackbar"
                >
                {{ text }}

                <template v-slot:action="{ attrs }">
                    <v-btn
                    color="red"
                    text
                    v-bind="attrs"
                    @click="snackbar = false"
                    >
                    Close
                    </v-btn>
                </template>
            </v-snackbar>

            </v-main>

        </v-app>
    </div>
</template>

<script>

export default {
    name: 'Login',
    components: {
    },

    data: () => ({
        showPassword: false,
        active: false,
        dialog: false,
        google: false,
        snackbar: false,
        text: 'Not Updated Yet !!'
    }),

    watch: {
        dialog (val) {
            if (!val) return

            setTimeout(() => (this.dialog = false, this.snackbar = true), 4000)
        },
    },
};
</script>
